import React, { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import axios from 'axios';
import {
    Box,
    Card,
    CardHeader,
    CardContent,
    TextField,
    InputAdornment,
    IconButton,
    Table,
    TableHead,
    TableRow,
    TableCell,
    TableBody,
    TableContainer,
    TablePagination,
    MenuItem,
    FormControl,
    Select,
    Tooltip,
    CircularProgress,
    Alert,
    Chip,
    Paper,
    List,
    ListItemButton,
    ListItemText,
    Divider,
    Typography,
    Popover,
    Checkbox,
    ListItemIcon,
} from '@mui/material';
import { Search, Clear, Refresh, Visibility, CheckCircleOutline, ErrorOutline } from '@mui/icons-material';
import { useAuth } from '../contexts/AuthContext';

// 🧩 kolon tanımları
const COLUMN_DEFS = [
    { key: 'name', label: 'Ad' },
    { key: 'surname', label: 'Soyad' },
    { key: 'email', label: 'E-posta' },
    { key: 'phone', label: 'Telefon' },
    { key: 'username', label: 'Kullanıcı adı' },
    { key: 'emailverified', label: 'E-posta Onay' },
    { key: 'created_at', label: 'Kayıt tarihi' },
];

const SORT_FIELDS = COLUMN_DEFS.filter(c => ['name','surname','email','phone','username','created_at'].includes(c.key))
    .map(c => ({ value: c.key, label: c.label }));

const SORT_ORDERS = [
    { value: 'ASC', label: 'Artan' },
    { value: 'DESC', label: 'Azalan' },
];

export default function UsersList({ companyId, initialLimit = 20, sx }) {
    const { token } = useAuth();
    const API_URL = `${process.env.REACT_APP_API_URL}/search-users`;

    const [rows, setRows] = useState([]);
    const [limit, setLimit] = useState(initialLimit);
    const [page, setPage] = useState(0);
    const [sortBy, setSortBy] = useState('name');
    const [sortOrder, setSortOrder] = useState('ASC');

    const [total, setTotal] = useState(0);
    const [loading, setLoading] = useState(false);
    const [errorMsg, setErrorMsg] = useState('');

    const [searchTerm, setSearchTerm] = useState('');
    const [suggestions, setSuggestions] = useState([]);
    const [suggOpen, setSuggOpen] = useState(false);
    const [suggLoading, setSuggLoading] = useState(false);
    const searchRef = useRef(null);
    const suggAbortRef = useRef(null);

    // 🧩 kolon görünürlük kontrolü
    const [visibleCols, setVisibleCols] = useState(() =>
        COLUMN_DEFS.reduce((acc, c) => ({ ...acc, [c.key]: true }), {})
    );
    const [anchorEl, setAnchorEl] = useState(null);
    const openColsMenu = (e) => setAnchorEl(e.currentTarget);
    const closeColsMenu = () => setAnchorEl(null);

    const toggleCol = (key) => {
        setVisibleCols((prev) => ({ ...prev, [key]: !prev[key] }));
    };

    const offset = useMemo(() => page * limit, [page, limit]);
    const authHeaders = useMemo(
        () => ({
            headers: { 'x-access-token': token, 'Content-Type': 'application/json' },
        }),
        [token]
    );

    const fetchUsers = useCallback(async () => {
        if (!companyId) {
            setErrorMsg('companyId gerekli');
            return;
        }
        try {
            setLoading(true);
            setErrorMsg('');
            const body = { companyId, searchTerm, limit, offset, sortBy, sortOrder };
            const { data } = await axios.post(API_URL, body, authHeaders);
            if (data?.success) {
                setRows(data.data?.users ?? []);
                setTotal(data.data?.pagination?.total ?? 0);
            } else {
                setRows([]); setTotal(0);
                setErrorMsg(data?.message || 'Kullanıcılar alınamadı');
            }
        } catch (err) {
            setRows([]); setTotal(0);
            setErrorMsg(err?.response?.data?.message || err.message);
        } finally {
            setLoading(false);
        }
    }, [API_URL, authHeaders, companyId, limit, offset, searchTerm, sortBy, sortOrder]);

    useEffect(() => { fetchUsers(); }, [fetchUsers]);

    // 🔎 arama önerileri
    useEffect(() => {
        if (!searchTerm || searchTerm.trim().length < 2) {
            setSuggestions([]); setSuggOpen(false); return;
        }
        const handle = setTimeout(async () => {
            try {
                setSuggLoading(true); setSuggOpen(true);
                if (suggAbortRef.current) suggAbortRef.current.abort();
                const controller = new AbortController();
                suggAbortRef.current = controller;
                const body = { companyId, searchTerm, limit: 5, offset: 0, sortBy: 'name', sortOrder: 'ASC' };
                const { data } = await axios.post(API_URL, body, { ...authHeaders, signal: controller.signal });
                setSuggestions(data?.success ? data.data?.users ?? [] : []);
            } catch { setSuggestions([]); } finally { setSuggLoading(false); }
        }, 300);
        return () => clearTimeout(handle);
    }, [API_URL, authHeaders, companyId, searchTerm]);

    const renderVerifyChip = (flag) =>
        flag ? (
            <Chip size="small" color="success" icon={<CheckCircleOutline />} label="Onaylandı" />
        ) : (
            <Chip size="small" color="warning" icon={<ErrorOutline />} label="Bekleniyor" />
        );

    const formatDate = (d) =>
        d
            ? new Date(d).toLocaleDateString('tr-TR', {
                day: 'numeric',
                month: 'long',
                year: 'numeric',
            })
            : '-';

    return (
        <Card sx={{ ...sx }}>
            <CardHeader
                title="Kullanıcılar"
                action={
                    <Box sx={{ display: 'flex', gap: 1, alignItems: 'center' }}>
                        <FormControl size="small">
                            <Select value={sortBy} onChange={(e) => { setSortBy(e.target.value); setPage(0); }}>
                                {SORT_FIELDS.map((f) => (
                                    <MenuItem key={f.value} value={f.value}>{f.label}</MenuItem>
                                ))}
                            </Select>
                        </FormControl>

                        <FormControl size="small">
                            <Select value={sortOrder} onChange={(e) => { setSortOrder(e.target.value); setPage(0); }}>
                                {SORT_ORDERS.map((o) => (
                                    <MenuItem key={o.value} value={o.value}>{o.label}</MenuItem>
                                ))}
                            </Select>
                        </FormControl>

                        {/* Kolon görünüm menüsü */}
                        <Tooltip title="Görünüm">
                            <IconButton onClick={openColsMenu}>
                                <Visibility />
                            </IconButton>
                        </Tooltip>
                        <Popover
                            open={Boolean(anchorEl)}
                            anchorEl={anchorEl}
                            onClose={closeColsMenu}
                            anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
                            transformOrigin={{ vertical: 'top', horizontal: 'right' }}
                            PaperProps={{ sx: { width: 220, p: 1 } }}
                        >
                            <Typography variant="subtitle2" sx={{ px: 1, pb: 0.5 }}>
                                Görünecek Sütunlar
                            </Typography>
                            <Divider sx={{ mb: 0.5 }} />
                            {COLUMN_DEFS.map((c) => (
                                <ListItemButton key={c.key} dense onClick={() => toggleCol(c.key)}>
                                    <ListItemIcon>
                                        <Checkbox
                                            edge="start"
                                            size="small"
                                            checked={visibleCols[c.key]}
                                            tabIndex={-1}
                                            disableRipple
                                        />
                                    </ListItemIcon>
                                    <ListItemText primary={c.label} />
                                </ListItemButton>
                            ))}
                        </Popover>

                        {/* Arama */}
                        <Box sx={{ position: 'relative' }}>
                            <TextField
                                inputRef={searchRef}
                                size="small"
                                placeholder="Ara (isim, email, tel, username)"
                                value={searchTerm}
                                onChange={(e) => { setSearchTerm(e.target.value); setPage(0); }}
                                InputProps={{
                                    startAdornment: (
                                        <InputAdornment position="start">
                                            <Search fontSize="small" />
                                        </InputAdornment>
                                    ),
                                    endAdornment: (
                                        <InputAdornment position="end">
                                            {suggLoading ? (
                                                <CircularProgress size={16} />
                                            ) : searchTerm ? (
                                                <IconButton size="small" onClick={() => setSearchTerm('')}>
                                                    <Clear fontSize="small" />
                                                </IconButton>
                                            ) : null}
                                        </InputAdornment>
                                    ),
                                }}
                            />
                        </Box>

                        <Tooltip title="Yenile">
                            <IconButton onClick={fetchUsers}>
                                <Refresh />
                            </IconButton>
                        </Tooltip>
                    </Box>
                }
            />

            <CardContent sx={{ pt: 0 }}>
                {errorMsg && <Alert severity="error" sx={{ mb: 2 }}>{errorMsg}</Alert>}

                <TableContainer>
                    <Table size="small">
                        <TableHead>
                            <TableRow>
                                {COLUMN_DEFS.filter((c) => visibleCols[c.key]).map((c) => (
                                    <TableCell key={c.key}>{c.label}</TableCell>
                                ))}
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {loading ? (
                                <TableRow>
                                    <TableCell colSpan={COLUMN_DEFS.length}>
                                        <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                                            <CircularProgress size={18} />
                                            <Typography variant="body2" color="text.secondary">
                                                Yükleniyor…
                                            </Typography>
                                        </Box>
                                    </TableCell>
                                </TableRow>
                            ) : rows.length === 0 ? (
                                <TableRow>
                                    <TableCell colSpan={COLUMN_DEFS.length}>
                                        <Typography variant="body2" color="text.secondary">Kayıt bulunamadı.</Typography>
                                    </TableCell>
                                </TableRow>
                            ) : (
                                rows.map((u) => (
                                    <TableRow key={u.id}>
                                        {visibleCols.name && <TableCell>{u.name}</TableCell>}
                                        {visibleCols.surname && <TableCell>{u.surname}</TableCell>}
                                        {visibleCols.email && <TableCell>{u.email}</TableCell>}
                                        {visibleCols.phone && <TableCell>{u.phone || '-'}</TableCell>}
                                        {visibleCols.username && <TableCell>{u.username}</TableCell>}
                                        {visibleCols.emailverified && <TableCell>{renderVerifyChip(Boolean(u.emailverified))}</TableCell>}
                                        {visibleCols.created_at && <TableCell>{formatDate(u.created_at)}</TableCell>}
                                    </TableRow>
                                ))
                            )}
                        </TableBody>
                    </Table>
                </TableContainer>

                <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', mt: 1 }}>
                    <Typography variant="body2" color="text.secondary">Toplam: {total}</Typography>
                    <TablePagination
                        component="div"
                        count={total}
                        page={page}
                        onPageChange={(_, newPage) => setPage(newPage)}
                        rowsPerPage={limit}
                        onRowsPerPageChange={(e) => { setLimit(parseInt(e.target.value, 10)); setPage(0); }}
                        rowsPerPageOptions={[10, 20, 50, 100]}
                        labelRowsPerPage="Sayfa başına"
                        labelDisplayedRows={({ from, to, count }) => `${from}-${to} / ${count}`}
                    />
                </Box>
            </CardContent>
        </Card>
    );
}
